.App {
  text-align: center;
}

.App-header {
  background-color: blue;
  background: linear-gradient(rgba(255,255,255,0.6), rgba(255,255,255,0.7)),url(./bg.jpg);
  background-size: cover;
  background-postion: center center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 2vmin);
  color: black;
}


a.learningCategory {
  font-size: 0.8em;
  border: 1px solid black;
  padding:0;

  margin:0.2em;
  padding-left:0.5em;
  padding-right:0.5em;
  font-family:bookmania,serif;
  font-style:normal;
  color: black;
  text-decoration:none;
  border-radius:0.8em;
}



a.learningCategory:hover, a.selectedLearningCategory {
  background:black;
  color:white;
}

footer {
  position: fixed;
  bottom:0;
  right:0;
  font-size:0.6em;
  margin:0.2em;
}

.PortraitsHeader {
  /*background: rgba(0,0,0,0.8);*/
  color:white;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  flex-direction:row;
  justify-content:space-around;
  z-index: 20;
  height: 200px;
  user-select: none;
}

.PortraitsLeft {
  color:white;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 5vw;
  display: flex;
  align-items: center;
  flex-direction:column;
  justify-content:space-around;
  z-index: 20;
  height: 100vh;
}
.PortraitsLeft img {
  width: 100%;
  filter: grayscale(100%);
}
.PortraitsLeft img:hover{
  width: 100%;
  filter: grayscale(0%);
}

figcaption {
  font-size:0.4em;
  margin-bottom:1em;
}

video {
  width: 60vmin;
}

.MapsMedia img {
  width: 50vmin;
}

nav img {
  height:30vmin;
  margin: 0.25em;
  filter:grayscale(100%);
  /*mix-blend-mode: multiply;*/
  border-bottom:5px solid rgba(0,0,0,0);
}

.selectedParticipant img  {
  filter:grayscale(0%);
  border-top:5px solid blue;
}

.Maps {
   padding:0;
   margin:0;
   width:100vmax;
   height:100vmin;
}

.MapsBG {
  min-height:100%;
  overflow: scroll;
}

.MapsMedia {
  display: block;
  position: absolute;
  z-index: 10;
  text-align:left;
}

.PortraitsHeader img {
  height: 100%;
}

.Maps img {
  height:100%;
}

.darkbg {
  width:100vw;
  height:100vh;
  background:rgba(0,0,0,0.95);
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  color:white;
}

#film {
  margin:0 auto;
  display:block;
}

iframe {
  background: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,1)),grey;
  border:none;
}




.grid {
  align-items: flex-start;
  display: flex;
  flex-flow: row wrap;
  padding: 25px 20px;
  overflow-y: auto;
  max-height: calc(100% - 100px);
  color:black;
}

.learning {
  background-color: #fff;
  /*border:1px solid black;*/
  text-align:left;
  /*font-weight:bold;*/
  font-size:0.7em;
  margin: 0.5em;
  /*cursor: grab;*/
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 283px;
  padding: 15px;
  position: relative;
  /*user-select: none;*/
}

.learningFootnote {

  font-size:0.6em;
  position:absolute;
  bottom:1em;
  padding-right:1.5em;
}
